import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { LoginCredentials } from '../../types/auth';
import api from '../../services/api';
import { useAuth } from '../../context/AuthContext';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-hot-toast';
import { IconType } from 'react-icons';
import { FiMail, FiLock, FiEye, FiEyeOff, FiAlertCircle } from 'react-icons/fi';
import { motion, AnimatePresence } from 'framer-motion';

interface IconWrapperProps {
  icon: IconType;
  className?: string;
  'aria-hidden'?: boolean | string;
}

const IconWrapper: React.FC<IconWrapperProps> = ({ icon: Icon, ...props }) => {
  const Component = Icon as React.ElementType;
  return <Component {...props} />;
};

export const LoginForm = () => {
  const { register, handleSubmit, formState: { errors } } = useForm<LoginCredentials>();
  const { login } = useAuth();
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();
  const [loginAttempts, setLoginAttempts] = useState(0);
  const MAX_ATTEMPTS = 5;
  const LOCKOUT_TIME = 15; // minutos
  const [isLocked, setIsLocked] = useState(false);
  const [lockTimer, setLockTimer] = useState(0);

  const onSubmit = async (data: LoginCredentials) => {
    if (isLocked) {
      toast.error(`Cuenta bloqueada. Intente nuevamente en ${lockTimer} minutos`);
      return;
    }

    const loadingToast = toast.loading('Iniciando sesión...');
    try {
      setIsLoading(true);
      console.log('Intentando login con:', data);
      
      const response = await api.post('/users/login', data);
      console.log('Respuesta del servidor:', response.data);
      
      login(response.data.token);
      setLoginAttempts(0); // Reset intentos al lograr login exitoso
      toast.success('¡Bienvenido al sistema!', {
        id: loadingToast,
        duration: 3000,
        icon: '👋',
      });
      navigate('/dashboard');
      
    } catch (error: any) {
      setLoginAttempts(prev => prev + 1);
      
      if (error.response?.status === 429) {
        setIsLocked(true);
        startLockoutTimer();
        toast.error('Demasiados intentos. Cuenta bloqueada temporalmente', {
          id: loadingToast,
          duration: 5000,
        });
      } else {
        console.error('Error en login:', error.response?.data || error);
        toast.error(error.response?.data?.message || 'Error al iniciar sesión', {
          id: loadingToast,
        });
      }
    } finally {
      setIsLoading(false);
    }
  };

  const startLockoutTimer = () => {
    setLockTimer(LOCKOUT_TIME);
    const interval = setInterval(() => {
      setLockTimer(prev => {
        if (prev <= 1) {
          clearInterval(interval);
          setIsLocked(false);
          setLoginAttempts(0);
          return 0;
        }
        return prev - 1;
      });
    }, 60000); // actualizar cada minuto
  };

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className="space-y-6"
    >
      {/* Advertencia de intentos */}
      <AnimatePresence>
        {loginAttempts > 2 && !isLocked && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="bg-amber-50 border-l-4 border-amber-400 p-4 rounded-md"
          >
            <div className="flex items-center">
              <IconWrapper 
                icon={FiAlertCircle}
                className="text-amber-400 w-5 h-5 mr-2"
                aria-hidden="true"
              />
              <p className="text-sm text-amber-700">
                Quedan {MAX_ATTEMPTS - loginAttempts} intentos antes del bloqueo temporal
              </p>
            </div>
          </motion.div>
        )}
        {isLocked && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="bg-red-50 border-l-4 border-red-400 p-4 rounded-md"
          >
            <div className="flex items-center">
              <IconWrapper 
                icon={FiAlertCircle}
                className="text-red-400 w-5 h-5 mr-2"
                aria-hidden="true"
              />
              <div>
                <p className="text-sm text-red-700 font-medium">
                  Cuenta temporalmente bloqueada
                </p>
                <p className="text-xs text-red-600 mt-1">
                  Intente nuevamente en {lockTimer} minutos
                </p>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
        <div className="space-y-5">
          {/* Campo Email */}
          <motion.div
            initial={{ x: -20, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ delay: 0.1 }}
            className="space-y-2"
          >
            <label className="block text-sm font-medium text-gray-700 mb-1.5 tracking-wide">
              Correo Electrónico
            </label>
            <div className="relative group">
              <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                <IconWrapper 
                  icon={FiMail} 
                  className="h-5 w-5 text-gray-400 group-hover:text-indigo-500 transition-colors" 
                  aria-hidden="true" 
                />
              </div>
              <input
                type="email"
                {...register('email', { 
                  required: 'Email es requerido',
                  pattern: {
                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                    message: 'Correo electrónico inválido'
                  }
                })}
                disabled={isLocked}
                className={`
                  block w-full pl-11 pr-4 py-3.5
                  border border-gray-200 rounded-xl
                  focus:ring-2 focus:ring-indigo-500 focus:border-transparent 
                  transition duration-200 ease-in-out 
                  placeholder-gray-400 bg-white/70
                  hover:bg-white hover:shadow-sm
                  disabled:bg-gray-100 disabled:cursor-not-allowed
                  text-gray-700 text-base
                  ${errors.email ? 'border-red-300 focus:ring-red-500' : ''}
                `}
                placeholder="ejemplo@email.com"
              />
              <AnimatePresence>
                {errors.email && (
                  <motion.p 
                    initial={{ opacity: 0, y: -10 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -10 }}
                    className="mt-2 text-sm text-red-500 flex items-center"
                  >
                    <IconWrapper 
                      icon={FiAlertCircle}
                      className="w-4 h-4 mr-1"
                      aria-hidden="true"
                    />
                    {errors.email.message}
                  </motion.p>
                )}
              </AnimatePresence>
            </div>
          </motion.div>

          {/* Campo Contraseña */}
          <motion.div
            initial={{ x: -20, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ delay: 0.2 }}
            className="space-y-2"
          >
            <label className="block text-sm font-medium text-gray-700 mb-1.5 tracking-wide">
              Contraseña
            </label>
            <div className="relative group">
              <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                <IconWrapper 
                  icon={FiLock} 
                  className="h-5 w-5 text-gray-400 group-hover:text-indigo-500 transition-colors" 
                  aria-hidden="true" 
                />
              </div>
              <input
                type={showPassword ? "text" : "password"}
                {...register('password')}
                disabled={isLocked}
                className={`
                  block w-full pl-11 pr-12 py-3.5
                  border border-gray-200 rounded-xl
                  focus:ring-2 focus:ring-indigo-500 focus:border-transparent 
                  transition duration-200 ease-in-out 
                  placeholder-gray-400 bg-white/70
                  hover:bg-white hover:shadow-sm
                  disabled:bg-gray-100 disabled:cursor-not-allowed
                  text-gray-700 text-base
                `}
                placeholder="••••••••"
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                disabled={isLocked}
                className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-400 hover:text-gray-600 disabled:text-gray-300"
              >
                <IconWrapper 
                  icon={showPassword ? FiEyeOff : FiEye}
                  className="h-5 w-5" 
                  aria-hidden="true"
                />
              </button>
            </div>
          </motion.div>

          {/* Botón Submit */}
          <motion.button
            type="submit"
            disabled={isLoading || isLocked}
            className={`
              w-full py-4 px-6 
              flex items-center justify-center
              text-white font-medium tracking-wide
              rounded-xl text-base
              transition-all duration-300 
              transform hover:scale-[1.02]
              focus:outline-none focus:ring-2 focus:ring-offset-2 
              shadow-md hover:shadow-lg
              ${isLocked 
                ? 'bg-gray-400 cursor-not-allowed' 
                : 'bg-indigo-600 hover:bg-indigo-700 focus:ring-indigo-500'
              }
              disabled:opacity-50 disabled:hover:scale-100
              relative overflow-hidden
            `}
            whileHover={{ scale: isLocked ? 1 : 1.02 }}
            whileTap={{ scale: isLocked ? 1 : 0.98 }}
          >
            <span className="relative z-10 flex items-center">
              {isLoading ? (
                <motion.div 
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  className="flex items-center"
                >
                  <svg className="animate-spin -ml-1 mr-3 h-5 w-5" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none" />
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                  </svg>
                  Iniciando sesión...
                </motion.div>
              ) : (
                'Iniciar Sesión'
              )}
            </span>
          </motion.button>
        </div>
      </form>
    </motion.div>
  );
}; 