import React from 'react';
import { LoginForm } from '../../components/auth/LoginForm';
import logo from '../../assets/logo.png';
import { motion } from 'framer-motion';
import { FiBookOpen, FiShield, FiAward } from 'react-icons/fi';
import { IconType } from 'react-icons';

// Componente IconWrapper para manejar los iconos
interface IconWrapperProps {
  icon: IconType;
  className?: string;
  'aria-hidden'?: boolean | string;
}

const IconWrapper: React.FC<IconWrapperProps> = ({ icon: Icon, ...props }) => {
  const Component = Icon as React.ElementType;
  return <Component {...props} />;
};

export const LoginPage = () => {
  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      {/* Fondo decorativo mejorado */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute -top-1/2 -right-1/2 w-[1000px] h-[1000px] bg-gradient-to-br from-[#1a237e]/30 to-[#283593]/20 rounded-full blur-3xl animate-pulse-slow"></div>
        <div className="absolute -bottom-1/2 -left-1/2 w-[1000px] h-[1000px] bg-gradient-to-tr from-[#1a237e]/20 to-[#283593]/10 rounded-full blur-3xl"></div>
      </div>

      {/* Contenedor principal */}
      <div className="relative min-h-screen flex items-center justify-center p-4">
        <div className="w-full max-w-[1200px] bg-white/90 backdrop-blur-xl rounded-[2rem] shadow-2xl overflow-hidden flex flex-col md:flex-row">
          
          {/* Panel izquierdo - Información */}
          <motion.div 
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            className="w-full md:w-[55%] p-8 md:p-12 bg-gradient-to-br from-[#1a237e] to-[#283593] text-white relative overflow-hidden"
          >
            {/* Decoración de fondo mejorada */}
            <div className="absolute inset-0">
              <div className="absolute inset-0 opacity-10"
                style={{
                  backgroundImage: `radial-gradient(circle at 2px 2px, rgba(255,255,255,0.15) 2px, transparent 0)`,
                  backgroundSize: '30px 30px'
                }}
              ></div>
              <div className="absolute top-0 right-0 w-[800px] h-[800px] bg-white/5 rounded-full translate-x-1/2 -translate-y-1/2 blur-2xl"></div>
            </div>

            {/* Contenido */}
            <div className="relative z-10">
              {/* Logo y título */}
              <motion.div
                initial={{ y: -20, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.2, type: "spring", stiffness: 100 }}
                className="flex items-center gap-6 mb-12"
              >
                <img 
                  src={logo} 
                  alt="MV Abogados" 
                  className="w-24 h-24 object-contain drop-shadow-2xl hover:scale-105 transition-transform duration-500" 
                />
                <div>
                  <h1 className="text-3xl font-light tracking-wider">MV ABOGADOS</h1>
                  <p className="text-white/70 tracking-widest text-sm">ESTUDIO JURÍDICO</p>
                </div>
              </motion.div>

              {/* Frase inspiradora */}
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.3 }}
                className="mb-12 p-6 border-l-2 border-white/20"
              >
                <p className="text-xl font-light italic text-white/90">
                  "En la justicia encontramos la base de la paz social y el fundamento de una sociedad próspera."
                </p>
                <p className="text-sm text-white/60 mt-2">- Ulpiano</p>
              </motion.div>

              {/* Características */}
              <motion.div
                initial={{ y: 20, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.4 }}
                className="space-y-8 mb-12"
              >
                <div className="flex items-start gap-4">
                  <IconWrapper 
                    icon={FiBookOpen}
                    className="w-6 h-6 mt-1 text-white/80" 
                    aria-hidden="true"
                  />
                  <div>
                    <h3 className="text-lg font-medium mb-2">Excelencia Profesional</h3>
                    <p className="text-white/70 leading-relaxed">Más de 25 años de experiencia en asesoría jurídica integral.</p>
                  </div>
                </div>
                <div className="flex items-start gap-4">
                  <IconWrapper 
                    icon={FiShield}
                    className="w-6 h-6 mt-1 text-white/80" 
                    aria-hidden="true"
                  />
                  <div>
                    <h3 className="text-lg font-medium mb-2">Defensa Especializada</h3>
                    <p className="text-white/70 leading-relaxed">Protegemos sus derechos con dedicación y profesionalismo.</p>
                  </div>
                </div>
              </motion.div>

              {/* Footer actualizado */}
              <div className="flex justify-center items-center space-x-8 text-sm">
                <motion.div 
                  whileHover={{ scale: 1.05 }}
                  className="flex flex-col items-center space-y-2"
                >
                  <IconWrapper 
                    icon={FiBookOpen}
                    className="w-5 h-5 text-white/80" 
                    aria-hidden="true"
                  />
                  <span className="text-white/80 font-light tracking-wider">
                    Asesoría Legal
                  </span>
                </motion.div>
                <span className="w-1.5 h-1.5 rounded-full bg-white/30 mt-6"></span>
                <motion.div 
                  whileHover={{ scale: 1.05 }}
                  className="flex flex-col items-center space-y-2"
                >
                  <IconWrapper 
                    icon={FiShield}
                    className="w-5 h-5 text-white/80" 
                    aria-hidden="true"
                  />
                  <span className="text-white/80 font-light tracking-wider">
                    Defensa Jurídica
                  </span>
                </motion.div>
                <span className="w-1.5 h-1.5 rounded-full bg-white/30 mt-6"></span>
                <motion.div 
                  whileHover={{ scale: 1.05 }}
                  className="flex flex-col items-center space-y-2"
                >
                  <IconWrapper 
                    icon={FiAward}
                    className="w-5 h-5 text-white/80" 
                    aria-hidden="true"
                  />
                  <span className="text-white/80 font-light tracking-wider">
                    Consultoría Legal
                  </span>
                </motion.div>
              </div>
            </div>
          </motion.div>

          {/* Panel derecho - Login */}
          <motion.div 
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            className="w-full md:w-[45%] p-8 md:p-12 flex flex-col justify-center bg-gradient-to-b from-white/80 to-white/50"
          >
            <motion.div
              initial={{ y: 20, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ delay: 0.3 }}
              className="max-w-sm mx-auto w-full"
            >
              <div className="text-center mb-12">
                <motion.h2 
                  initial={{ y: -20 }}
                  animate={{ y: 0 }}
                  className="text-4xl font-light text-gray-900 mb-4"
                >
                  Bienvenido
                </motion.h2>
                <motion.div
                  initial={{ y: -10, opacity: 0 }}
                  animate={{ y: 0, opacity: 1 }}
                  transition={{ delay: 0.2 }}
                  className="relative"
                >
                  <p className="text-gray-600 text-lg">
                    Acceda a su portal legal personalizado
                  </p>
                  <div className="absolute -bottom-4 left-1/2 transform -translate-x-1/2 w-16 h-1 bg-gradient-to-r from-transparent via-indigo-500/30 to-transparent"></div>
                </motion.div>
              </div>

              <motion.div 
                initial={{ y: 20, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.4 }}
                className="bg-white/80 backdrop-blur-sm rounded-2xl p-8 shadow-[0_8px_30px_rgb(0,0,0,0.05)] border border-gray-100/50 hover:shadow-lg transition-all duration-300"
              >
                <LoginForm />
              </motion.div>

              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.5 }}
                className="mt-12 text-center space-y-2"
              >
                <div className="flex items-center justify-center space-x-2 text-sm text-gray-500">
                  <span>© {new Date().getFullYear()}</span>
                  <span className="w-1 h-1 rounded-full bg-gray-300"></span>
                  <span>MV Abogados</span>
                </div>
                <p className="text-xs text-gray-400">
                  Todos los derechos reservados
                </p>
              </motion.div>
            </motion.div>
          </motion.div>
        </div>
      </div>
    </div>
  );
}; 